.advantages {
  padding: 108px 0 129px;
  background-color: $add-white;

  @media (max-width: $vp-1366) {
    padding: 0;
  }
}

.advantages .container {
  @media (max-width: $vp-768) {
    padding: 63px 46px 70px;
  }
}

.advantages .title {
  @media (max-width: $vp-768) {
    margin-bottom: 52px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 340px 240px 340px;
  grid-template-rows: repeat(2, 1fr);
  gap: 0 60px;
  list-style: none;
  padding: 0;
  margin: 0;

  @media (max-width: $vp-1366) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: min-content max-content min-content;
    gap: 50px 0;
  }

  @media (max-width: $vp-768) {
    display: flex;
    flex-direction: column;
    gap: 50px;
  }
}

.advantages__list li {
  display: flex;
  position: relative;

  @media (max-width: $vp-768) {
    flex-direction: column;
    text-align: center;
    padding: 0;
  }
}

.advantages__item {
  padding: 0 20px 0 0;

  &--first-item {
    grid-column-start: 1;
    grid-row-start: 1;
  }

  &--second-item {
    grid-column-start: 3;
    grid-row-start: 1;

    @media (max-width: $vp-1366) {
      grid-column-start: 2;
    }
  }

  &--third-item {
    grid-column-start: 2;
    grid-row-start: 1;
    grid-row-end: 4;
    flex-direction: column;
    background-color: $comments-back;
    color: $default-white;
    padding: 66px 34px;
    text-align: center;

    @media (max-width: $vp-1366) {
      grid-column-start: 1;
      grid-column-end: 4;
      grid-row-start: 2;
      grid-row-end: 3;
      margin: 0 163px;
      padding: 45px 130px;
    }

    @media (max-width: $vp-768) {
      margin: 0 -46px;
      padding: 50px;
    }
  }

  &--fourth-item {
    grid-column-start: 1;
    grid-row-start: 2;

    @media (max-width: $vp-1366) {
      grid-row-start: 3;
      grid-column-start: 1;
    }
  }

  &--fifth-item {
    grid-column-start: 3;
    grid-row-start: 2;

    @media (max-width: $vp-1366) {
      grid-row-start: 3;
      grid-column-start: 2;
    }
  }
}

.advantages__item--first-item::before,
.advantages__item--second-item::before {
  content: "";
  background-color: rgba(209, 10, 66, 0.2);
  height: 2px;
  width: 100%;
  bottom: 5%;
  left: 0;
  position: absolute;

  @media (max-width: $vp-1366) {
    display: none;
  }
}

.advantages__item-wrapper {
  display: block;
  margin-right: 30px;
}

.advantages__subtitle {
  font-size: 71px;
  font-weight: 700;
  margin-bottom: 12px;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(128deg, #d10a42 0%, #0041ca 45.58%);

  @media (max-width: $vp-1366) {
    font-size: 58px;
  }

  &--white {
    font-size: 74px;
    -webkit-text-fill-color: $default-white;

    @media (max-width: $vp-1366) {
      font-size: 74px;
      margin: 0 0 20px;
    }
  }
}

.advantages__item-wrapper p {
  font-size: 14px;
  color: $additional-blue;
  margin: 0;
}

.advantages__title {
  font-size: 22px;
  font-weight: 700;
  line-height: 1.5;
  margin: 0 0 20px;

  @media (max-width: $vp-1366) {
    font-size: 20px;
    margin: 0 0 15px;
  }
}

.advantages__text {
  font-weight: 300;
  font-size: 16px;
  margin: 0;
}
